const randomNumber = Math.floor(Math.random() * 2);
let oddOrEven;

if (randomNumber === 0) {
  oddOrEven = "odd";
} else {
  oddOrEven = "even";
}

const ProjectContainer = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 60vw;
  margin: auto;
  grid-row-gap: 40px;
  grid-column-gap: 40px;
  margin-top: 3rem;
  margin-bottom: 3rem;
  transition: all 1s ease-out;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
`;

const GridItem = styled.div`
  font-size: 2.3rem;
  text-align: center;
  padding: 0;
  overflow: hidden;
  border-radius: 6px;

  @media (max-width: 768px) {
    &:nth-child(${oddOrEven}) {
      display: none;
    }

    & p {
      font-size: 1.5rem;
      width: 100%;
      padding: 1rem !important;
    }
  }

  & a {
    text-decoration: none;
  }

  & img {
    width: 100%;
    display: block;
    border-radius: 6px 6px 0 0;
    transition: all 0.3s;
    transform: scale(1);
    height: auto;
  }

  & p {
    color: white;
    text-align: center;
    background-color: rgb(26, 36, 54);
    margin: 0;
    padding: 1.5rem;
  }

  & span {
    height: 0;
    opacity: 0;
    vertical-align: center;
    transition: all 0.3s;
    font-size: 2rem;
    padding-left: 1rem;
    padding-right: 0.3rem;
  }

  &:hover {
    span {
      height: 1;
      opacity: 1;
      color: var(--neon-green);
    }
    img {
      filter: blur(1.3px);
      transform: scale(1.1);
    }
  }
`;

const Projects = () => {
  return (
    <div>
      <NeonText size="2rem">Some of my projects</NeonText>

      <ProjectContainer id="Projects">
        {projectsContent.map((project) => (
          <GridItem>
            <a href={project.url} target="_blank" rel="noopener noreferrer">
              <img src={project.image} alt={project.name} />

              <p>
                <span>{"<"}</span>
                {project.name}
                <span> {" />"}</span>
              </p>
            </a>
          </GridItem>
        ))}
      </ProjectContainer>
    </div>
  );
};

export default Projects;
