import React from "react";
import styled from "styled-components";
import { socialMedia } from "../constants/config";
import NeonText from "./layout/NeonText";

const StyledFooter = styled.footer`
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 30vh;
  border-top: 1px solid var(--neon-green-opacity2);
  width: 100vw;
  padding: 5rem;
  margin-bottom: 7rem;

  @media (max-width: 768px) {
    margin-bottom: 3rem;
    }
`;

const StyledSocialIcons = styled.ul`
  display: flex;
  list-style: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 4rem;

  @media (max-width: 768px) {
    font-size: 2rem;
    }
`;
const StyledItem = styled.li`
  margin-right: 3rem;
  margin-left: 3rem;
  transition: all 0.4s;

  @media (max-width: 768px) {
    margin-right: 2rem;
    margin-left: 2rem;
    }

  &:hover {
    transform: scale(1.4);
    cursor: pointer;
    color: ${(props) => props.hover};
  }

  & a {
    text-decoration: none;
    color: var(--dark-grey);

    &:hover {
      color: ${(props) => props.hover};
    }
  }
`;

const StyledTitle = styled.h3`
  margin-top: 0;
  color: var(--lightest-grey);

  @media (max-width: 768px) {
    font-size: 2.5rem;
    }
  
`

const Footer = () => {
  return (
    <StyledFooter id="Contact">
      <NeonText>Contact me</NeonText>
      <StyledTitle>Let's keep in touch!</StyledTitle>
      <StyledSocialIcons>
        {socialMedia.map((link) => (
          <StyledItem hover={link.color}>
            <a href={link.url}>
              <link.icon />
            </a>
          </StyledItem>
        ))}
      </StyledSocialIcons>
    </StyledFooter>
  );
};

export default Footer;
